import React, { Component } from 'react';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import './App.css';
import AppBar from 'material-ui/AppBar';
import RaisedButton from 'material-ui/RaisedButton';
import Subheader from 'material-ui/Subheader';
import {Card, CardActions, CardHeader, CardMedia, CardTitle, CardText} from 'material-ui/Card';
import Chip from 'material-ui/Chip';
import getMuiTheme from 'material-ui/styles/getMuiTheme';
import {red500, green500} from 'material-ui/styles/colors';
import {speakers} from 'toml!./2017/speaker.toml';
import Markdown from 'react-markdown';

const muiTheme = getMuiTheme({
    palette: {
        primary1Color: red500,
        accent1Color: green500
    },
    appBar: {
        height: 50,
    },
});

const EntryButton = () => {
    return <RaisedButton label="" href="https://osh.connpass.com/event/48114/" primary={true}/>
};

const Hero = () => {
    return (
        <Card>
            <CardTitle title="!!" subtitle="MAY THE ENGINEERING BE WITH YOU" />

            <CardMedia
                overlay={<CardTitle title="2017@" subtitle="2017225()" />}
            >
                <img src="/img/2017_header.jpg" />
            </CardMedia>
            <CardText>
                <h1>!!<br /><span style={{fontSize: -2, color: '#999'}}>MAY THE ENGINEERING BE WITH YOU</span></h1>
                <p>
                     ICTInformation and Communication Technology()IoTInternet of Things
                </p>
                <p>

                    IT
                </p>
                <p>
                    
                    
                </p>
                <p>
                    
                    
                </p>
            </CardText>
            <CardActions>
            </CardActions>
        </Card>
        );
};

const Speaker = props => {
    const chip_style = {
        margin: 4,
    };

    let chips = props.tags.map((tag) => {
        return (
            <Chip style={chip_style}>{tag}</Chip>
        );
    });

    const style = {
        display: 'flex',
        flexWrap: 'wrap',
        paddingBottom: 14,
        paddingLeft: 10,
        paddingRight: 10,
    };

    return (
        <Card>
            <CardTitle
                title={props.title}
            />
            <div style={style}>
                {chips}
            </div>
            <CardHeader
                title={props.name}
                subtitle={props.furi}
                avatar={props.avatar}
            />
            <CardText>
                <Markdown source={props.profile}/>
            </CardText>
        </Card>
    );
};

class App extends Component {
    render() {
        let Speakers = speakers.map((speaker) => {
            return (<Speaker {...speaker} />);
        });

        return (
            <MuiThemeProvider muiTheme={muiTheme}>
                <div>
                    <AppBar
                        title="2017@"
                        iconClassNameRight="muidocs-icon-navigation-expand-more"
                        titleStyle={{'font-size': 16}}
                    />

                    <Hero />

                    <Subheader></Subheader>
                    {Speakers}


                </div>
            </MuiThemeProvider>
        );
    }
}

export default App;



